@charset "UTF-8";
@font-face {
  font-family: "Main";
  src: url("~@/src/assets/Futura LT Heavy.otf"); }

html,
body {
  font-size: calc(100vw/375*100);
  height: 100%; }

@media screen and (min-width: 600px) {
  html {
    font-size: 160px; } }

@media screen and (min-width: 750px) {
  html {
    font-size: 195px; } }

#root {
  min-height: 100%;
  position: relative;
  overflow: hidden;
  padding-bottom: 0.8rem;
  box-sizing: border-box; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0; }
  to {
    opacity: 1; } }

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
main,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

/* Don't kill focus outline for keyboard users: http://24ways.org/2009/dont-lose-your-focus */
a:hover,
a:active {
  outline: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/*这里定义整个应用加载时的动画，进场前这里设置了缩放为0，
进场过程中缩放变为1，也就是100%，持续时间为500ms，
时间曲是ease-out，一种先快后慢的变化曲线*/
.appAppear-appear {
  opacity: 0;
  transform: scale(0); }

.appAppear-appear.appAppear-appear-active {
  opacity: 1;
  transform: scale(1);
  transition: 500ms ease-out; }

/*设置进场前透明度为0，放缩为0*/
.fade-enter {
  opacity: 0; }

/*设置进场过程中 透明度为1，放缩为1，放缩的位置为左上角，持续时间为500ms，时间曲是ease-out */
.fade-enter-active {
  opacity: 1;
  transition: all 500ms ease-out; }

/*设置退场前透明度为1，放缩为1*/
.fade-exit {
  opacity: 1; }

/*设置退场过程中 透明度为0，放缩为0，放缩的位置默认是中间，所以不设置也可以，
  持续时间为500ms，时间曲是ease-out */
.fade-exit-active {
  position: absolute;
  z-index: -1;
  opacity: 0;
  transition: all 0ms ease-out; }

.header {
  width: 700px;
  height: 100px;
  color: #24292e;
  text-align: center;
  margin: 0 auto;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .3s;
  font-family: Main; }
  .header .logo {
    margin: 10px 10px 10px 0;
    width: 30px;
    height: 30px;
    transition: all .3s;
    display: block;
    user-select: none; }
  .header nav {
    display: flex;
    align-items: center;
    padding: 80px 0;
    transition: all .4s; }
    .header nav span {
      font-size: 15px;
      line-height: 15px;
      display: block;
      margin: 0 30px;
      font-style: normal;
      font-family: Main;
      position: relative;
      z-index: 2;
      cursor: pointer;
      user-select: none; }
    .header nav .nav-active,
    .header nav span:hover {
      color: #c09c00; }

.header-sp {
  filter: invert(90%); }
  .header-sp .nav-active,
  .header-sp a:hover {
    filter: invert(90%); }

/* 手机版适配 */
.header-mobile {
  position: relative;
  z-index: 3; }
  .header-mobile .header-top {
    background: #24292e;
    position: relative;
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.1rem; }
    .header-mobile .header-top .logo,
    .header-mobile .header-top .fire {
      display: block;
      width: .3em;
      height: .3rem;
      filter: invert(97.5%); }
    .header-mobile .header-top .fire {
      width: .16rem;
      height: .16rem;
      margin-top: .04rem; }
    .header-mobile .header-top .fire-show {
      animation: fire .5s;
      transform-origin: left;
      animation-fill-mode: forwards; }
    .header-mobile .header-top .fire-hide {
      animation: fire .5s;
      transform-origin: left;
      animation-direction: reverse;
      animation-fill-mode: forwards; }
    .header-mobile .header-top .nav-title {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 20px;
      font-family: Main;
      color: #ffffff;
      margin-top: 0.02rem;
      margin-left: auto; }
  .header-mobile nav {
    position: absolute;
    left: 0;
    width: 100%; }
    .header-mobile nav span {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 0.5rem;
      font-size: 15px;
      text-align: center;
      font-style: normal;
      font-family: Main;
      background: #24292e;
      color: #ffffff; }
    .header-mobile nav .nav-active {
      color: #c09c00; }

.slide-enter {
  transform: translateY(-10rem); }

.slide-exit {
  transform: translateY(0); }

.slide-enter-active {
  transition: all 0.3s;
  transform: translateY(0); }

.slide-exit-active {
  transition: all 0.3s;
  transform: translateY(-10rem); }

@keyframes invertALl {
  0% {
    filter: invert(0); }
  100% {
    filter: invert(87.5%); } }

@keyframes fire {
  0% {
    transform: scale(0); }
  50% {
    transform: scale(1); } }
